
"use client";

import React from "react";
import Header from "@/components/Header";
import Sidebar from "@/components/Sidebar";

export default function LayoutDashboard({ children }) {
  return (
    <div>
      <div>
        <Header />
        <main>{children}</main>
      </div>
      <div>
        <Sidebar />
      </div>
    </div>
  );
}